<template>
  <div>
    <div class="row">
      <div class="col-12">
        <card type="chart">
          <template slot="header">
            <div class="row">
              <div class="col-sm-6" :class="isRTL ? 'text-right' : 'text-left'">
                <h4 class="card-title">모의고사 점수</h4>
              </div>
              <div class="col-sm-6">
                <div class="btn-group btn-group-toggle"
                     :class="isRTL ? 'float-left' : 'float-right'"
                     data-toggle="buttons">
                  <label v-for="(option, index) in bigLineChartCategories"
                         :key="option"
                         class="btn btn-sm btn-primary btn-simple"
                         :class="{active: bigLineChart.activeIndex === index}"
                         :id="index">
                    <input type="radio"
                           @click="initBigChart(index)"
                           name="options" autocomplete="off"
                           :checked="bigLineChart.activeIndex === index">
                    {{option}}
                  </label>
                </div>
              </div>
            </div>
          </template>
          <div class="chart-area">
            <line-chart style="height: 100%"
                        ref="bigChart"
                        chart-id="big-line-chart"
                        :chart-data="bigLineChart.chartData"
                        :gradient-colors="bigLineChart.gradientColors"
                        :gradient-stops="bigLineChart.gradientStops"
                        :extra-options="bigLineChart.extraOptions">
            </line-chart>
          </div>
        </card>
      </div>
    </div>
    
    
    <div class="row">
      <div class="col-7">
        <card :title="table2.title">
          <div class="table-responsive">
            <base-table :data="table2.data"
                        :columns="table2.columns"
                        thead-classes="text-primary">
            </base-table>
          </div>
        </card>
      </div>
      <div class="col-5">
        <card :title="table3.title">
          <div class="table-responsive">
            <base-table :data="table3.data"
                        :columns="table3.columns"
                        thead-classes="text-primary">
            </base-table>
          </div>
        </card>
      </div>
    </div>
    
    <div class="row">
    </div>

  </div>
</template>
<script>
  import LineChart from '@/components/Charts/LineChart';
  import * as chartConfigs from '@/components/Charts/config';
  import config from '@/config';
  import firebase from 'firebase';
  import { BaseTable } from "@/components";

  const tableColumns = ["시험일자", "지각속도", "공간능력", "자료해석", "언어논리", "한국사", "총점"];
  const table2Columns = ["영역", "자격증", "자격증번호", "취득일"];
  const table2Data = [
  {
    id: 1,
    영역: "한국어",
    자격증: "-",
    자격증번호: "-",
    취득일: "-",
  },
  {
    id: 2,
    영역: "전산",
    자격증: "문서실무사(KB) 2급",
    자격증번호: "191KY51-20000089",
    취득일: "2019.04.26",
  },
  {
    id: 3,
    영역: "한자",
    자격증: "-",
    자격증번호: "-",
    취득일: "-",
  },
  {
    id: 4,
    영역: "한국사",
    자격증: "한국사능력검정시험 3급",
    자격증번호: "23-109140",
    취득일: "2018.08.13",
  },
  {
    id: 5,
    영역: "무도",
    자격증: "태권도 4단",
    자격증번호: "03883502",
    취득일: "2017.08.01",
  },
  {
    id: 6,
    영역: "영어",
    자격증: "-",
    자격증번호: "-",
    취득일: "-",
  }
  ];

  const table3Columns = ["직위", "활동기간"];
  const table3Data = [
    {
      id: 1,
      직위: "학급반장",
      활동기간: "2017.03.03 ~ 2017.08.05",
    },
    {
      id: 2,
      직위: "학생자치회장",
      활동기간: "2017.08.07 ~ 2018.08.04",
    },
    {
      id: 3,
      직위: "-",
      활동기간: "-",
    },
    {
      id: 4,
      직위: "-",
      활동기간: "-",
    },
    {
      id: 5,
      직위: "-",
      활동기간: "-",
    },
    {
      id: 6,
      직위: "-",
      활동기간: "-",
    }
  ];

  export default {
    components: {
      LineChart,
      BaseTable
    },
    data() {
      return {
        bigLineChart: {
          allData:  [ [],[],[],[],[] ],
          activeIndex: 0,
          chartData: null,
          extraOptions: chartConfigs.purpleChartOptions,
          gradientColors: config.colors.primaryGradient,
          gradientStops: [1, 0.4, 0],
          categories: [],
          labels: [],
        },
        table1: {
          title: "모의고사 점수",
          columns: [...tableColumns],
          data: []
        },
        table2: {
          title: "자격증 취득 현황",
          columns: [...table2Columns],
          data: [...table2Data]
        },
        table3: {
          title: "리더십",
          columns: [...table3Columns],
          data: [...table3Data]
        },
        chart: {
          label: [],
          data: [ [], [], [], [], [], [] ],
          ydata: [ [], [], [], [], [], [] ],
          ydata_total: [ [], [], [], [], [], [] ]
        },
        modalVisible: false,
        editFormData:{
          year: '',
          month: '',
          score_1: '',
          score_2: '',
          score_3: '',
          score_4: '',
          score_5: '',
          id: ''
        }
      }
    },
    computed: {
      enableRTL() {
        return this.$route.query.enableRTL;
      },
      isRTL() {
        return this.$rtl.isRTL;
      },
      bigLineChartCategories() {
        return this.$t('dashboard.chartCategories');
      }
    },
    methods: {
      initBigChart(index) {
        let chartData = {
          datasets: [{
            fill: true,
            borderColor: config.colors.primary,
            borderWidth: 2,
            borderDash: [],
            borderDashOffset: 0.0,
            pointBackgroundColor: config.colors.primary,
            pointBorderColor: 'rgba(255,255,255,0)',
            pointHoverBackgroundColor: config.colors.primary,
            pointBorderWidth: 20,
            pointHoverRadius: 4,
            pointHoverBorderWidth: 15,
            pointRadius: 4,
            data: this.bigLineChart.allData[index]
          },
           {
            type: 'line',
            data: index == 5 ? this.chart.ydata[0] : null,
            backgroundColor: 'rgba(255, 68, 0, 0.4)',
            borderColor: '#e14eca'
          }],
          labels: this.bigLineChart.labels,
        }
        this.$refs.bigChart.updateGradients(chartData);
        this.bigLineChart.chartData = chartData;
        this.bigLineChart.activeIndex = index;
      }
    },
    mounted() {
      let email = firebase.auth().currentUser.email;
        firebase.firestore().collection('scoreInfo').where('email', '==', email).orderBy('date', 'desc').get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              this.table1.data.push({
                    시험일자 : doc.data().date,
                    지각속도 : doc.data().score_1,
                    공간능력 : doc.data().score_2,
                    자료해석 : doc.data().score_3,
                    언어논리 : doc.data().score_4,
                    한국사   : doc.data().score_5,
                    총점     : parseInt(doc.data().score_1) + parseInt(doc.data().score_2) + parseInt(doc.data().score_3) + parseInt(doc.data().score_4) + parseInt(doc.data().score_5),
                    docId   : doc.id,
              })
              this.chart.label.push(doc.data().date);
              this.chart.data[0].push(doc.data().score_1);
              this.chart.data[1].push(doc.data().score_2);
              this.chart.data[2].push(doc.data().score_3);
              this.chart.data[3].push(doc.data().score_4);
              this.chart.data[4].push(doc.data().score_5);
              this.chart.data[5].push(parseInt(doc.data().score_1) + parseInt(doc.data().score_2) + parseInt(doc.data().score_3) + parseInt(doc.data().score_4) + parseInt(doc.data().score_5));
              this.chart.ydata[0].push(12);
            })
            this.bigLineChart.labels = this.chart.label.reverse();
            this.bigLineChart.allData[0] = this.chart.data[0].reverse();
            this.bigLineChart.allData[1] = this.chart.data[1].reverse();
            this.bigLineChart.allData[2] = this.chart.data[2].reverse();
            this.bigLineChart.allData[3] = this.chart.data[3].reverse();
            this.bigLineChart.allData[4] = this.chart.data[4].reverse();
            this.bigLineChart.allData[5] = this.chart.data[5].reverse();
      }).then(() => {
        this.initBigChart(5);
      })

      this.i18n = this.$i18n;
      if (this.enableRTL) {
        this.i18n.locale = 'ar';
        this.$rtl.enableRTL();
      }
    },
    beforeDestroy() {
      if (this.$rtl.isRTL) {
        this.i18n.locale = 'en';
        this.$rtl.disableRTL();
      }
    }
  };

</script>
<style scoped>
.icon-simple-remove::before{
  color: white;
}
.test{
  top: -10%;
}
.table-responsive{
    overflow:hidden;
}
</style>